import React, { Component } from 'react'
import fawks14 from "./fawks14.jpg"
import './App.css';

class App extends Component {

    state = {
        date: new Date('2022-06-17T03:00:00'),
        dateShow: ""
    }

    componentDidMount() {
        requestAnimationFrame(this.getNewDate)
    }

    getNewDate = () => {
        this.setState({ dateShow: this.getTimerString(Math.abs( new Date() - this.state.date)) })
        setTimeout(() => requestAnimationFrame(this.getNewDate), 50);
    }

    getTimerString = (milSec) => {
        const days = Math.floor(milSec / 86400000);
        if (days === 0) {
            return new Date(new Date(milSec)).toISOString().slice(11, -1);
        } else {
            var dayText = " дней "
            if (days === 1) {
                dayText = " день "
            } else if (days === 2 || days === 3) {
                dayText = " дня "
            }
            return days+dayText+new Date(new Date(milSec)).toISOString().slice(11, -1);
        }
    }


  render() {

    return (
        <div className="App">
          <header className="App-header">
              <span className="textSpan" title={"той самий"}>Це Олександр</span>
              <img src={fawks14} className="photo" title={"Олександр який не курить"} alt="Олександр Константінов" />
              <span className="textSpan" title={"неймовырно але факт!"}>він не куре вже, аж</span>
              <span className="timerSpan" title={"не куре з "+this.state.date.getFullYear()+"/"+(1+this.state.date.getMonth())+"/"+this.state.date.getDate()}>{this.state.dateShow}</span>
              <span className="textSpan" title={"да прибуде з ним сила!"}>побажаемо ему успіху :)</span>
          </header>
        </div>
    );
  }
}

export default App;
